.container {
  height: 100vh;
  height: -moz-available;
  /* WebKit-based browsers will ignore this. */
  height: -webkit-fill-available;
  /* Mozilla-based browsers will ignore this. */
  height: stretch;
  background: #e0e0e0;
  overflow-y: auto;
}

.margin {
  align-items: center;
  flex-direction: column;
  display: flex;
  padding: 100px;
  min-height: 100vh;
}

.inner {
  background: #fff;
  min-height: calc(100vh - 200px);
  width: 100%;
  max-width: 1023px;
  padding: 75px;
  display: flex;
  gap: 75px;
}

.picker {
  flex: 0 0 auto;
}

.pickerGrid {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1rem;
  width: fit-content;
  margin: auto;
}

.swatches {
  flex: 1 1 auto;
  display: grid;
  grid-template-columns: auto;
  grid-gap: 1rem;
  align-content: start;
}

@media only screen and (max-width: 1023px) {
  .margin {
    padding: 0px;
  }
  .container {
    background: #fff;
  }
}

@media only screen and (max-width: 767px) {
  .inner {
    flex-direction: column;
  }
}
